.title {
	text-align: center;
	font-size: 92px;
	font-family: 'PlayfairDisplay', serif;
	color:#ffffff;
	letter-spacing: 2px;
	padding: 180px 0 100px 0;
	text-shadow: 5px 5px 10px #56534c; 
}

.subtitle {
	width: 560px;
	text-align: center;
	font-size: 22px;
	font-family: 'Maven Pro', sans-serif;
	color:#ffffff;
	line-height: 25px;
	margin: 0 auto;
	padding-bottom: 80px;
	text-shadow: 1px 1px 1px #56534c;
}

.btn-menu {
	display: block;
	margin: 0 auto;
	width: 200px;
	height: 50px;
	font-size: 14px;
	font-weight: 700;
	color:#ffffff;
	text-transform: uppercase;
	letter-spacing: 3px;
	background-color: #56534c;
	opacity: .90;
	border: none;
	cursor: pointer;
	text-shadow: 1px 1px 2px #56534c;
}

.btn-menu:hover {
	background-color: #979285;  
}

.btn {
	padding-bottom: 145px
}