.content {
	display: flex;
}

.recipe-cont {
	width: 25%;
}

.name-cont-info {
	width: 300px;
	height: 135px;	
	text-align: center;
	font-size: 28px;
	font-weight: 700;
	color: #a47d5e;
	padding: 50px 0 30px 0;
}

.recipe-cont img {
 width: 300px;
 height: 229px;
 display: block;
}

.cooking-info {
	text-align: center;
	font-size: 18px;
	color: #56534c;
	font-weight: 700;
	width: 31.8%;
	padding: 20px 0 5px 0;
	background-color: #ffffff;
	opacity: .8;
	margin-top: 10px;
}

.info-number {
	text-align: center;
	font-size: 28px;
	color: #a47d5e;
	font-weight: 700;
	width: 31.8%;
	background-color: #ffffff;
	opacity: .8;
}

.info-mins {
	text-align: center;
	font-size: 16px;
	font-weight: 700;
	color: #979285;
	padding: 3px 0;
	width: 31.8%;
	background-color: #ffffff;
	opacity: .8;
	margin-bottom: 50px;
	
}

.cooking-info:last-child, 
.info-number:last-child, 
.info-mins:last-child {
	border-left: 1px solid #979285;
}

.cooking-info:first-child, 
.info-number:first-child, 
.info-mins:first-child {
	border-right: 1px solid #979285;
}