.content {
	display: flex;

}

.right-content-filter,
.left-content-filter {
	width: 50%;
}

.right-content-filter {
	margin-right: 50px;
}

.name-cont {
	text-align: center;
	font-size: 28px;
	font-weight: 700;
	color: #a47d5e;
	padding: 50px 0 30px 0;
}

.recipe-filter img {
	display: block;
	width: 468px;
	height: 310px;
	margin: 0 auto;
}

.description-filter {
	text-align: justify;
	font-size: 16px;
	color: #56534c;
	font-family: 'Maven Pro', sans-serif;
	line-height: 22px;
	margin: 30px 0 0 20px;
	
}

.title-ingr-filter, .title-method-filter {
	text-align: center;
	font-family: 'Maven Pro', sans-serif;
	font-size: 20px;
	color: #56534c;
	font-weight: 700;
	padding: 20px 0;
	
}

.description-ingr-filter p {
	font-size: 16px;
	color: #56534c;
	font-family: 'Maven Pro', sans-serif;
	line-height: 22px;
	margin-left: 20px;
}

