.wrapper-two {
	border-top: 1px solid #bfbbb7;
	border-bottom: 1px solid #bfbbb7;
	background-color: #eeece1;
}

.title-dish {
	width: 690px;
	text-align: center;
	font-size: 48px;
	color: #56534c;
	font-weight: 700;
	margin: 0 auto;
	padding-top: 12px;
}

.subtitle-dish {
	text-align: center;
	font-size: 14px;
	color: #a47d5e;
	text-transform: uppercase;
	padding: 5px 0 12px 0;
}

.left-content {
	width: 25%;
	margin-top: 150px;
}

.right-content {
	width: 75%;
	position: relative;
}

.title-left-cont {
	text-align: center;
	font-size: 26px;
	font-weight: 700;
	color: #a47d5e;
	padding-top: 20px;

}


.subtitle-left-cont {
	text-align: center;
	font-size: 18px;
	color: #56534c;
	font-family: 'Maven Pro', sans-serif;
	line-height: 20px;
	padding-top: 20px;
}

.stats {
	display: flex;
	margin-top: 10px;
}

.calories{
	width: 35%;
	padding-top: 20px;
	font-size: 18px;
	font-family: 'Maven Pro', sans-serif;
	color: #56534c;
	font-weight: 700;
}

.calories-num {
	width: 65%;
	padding-top: 20px;
	font-size: 18px;
	font-family: 'Maven Pro', sans-serif;
	color: #56534c;
}

.title-right-cont {
	text-align: center;
	font-size: 32px;
	font-weight: 700;
	color: #a47d5e;
	padding-top: 50px;
}

.recipe img {
	display: block;
	width: 820px;
	height: 528px;
	margin: 30px 0 0 125px;
	border: 10px solid #ffffff;
	margin-bottom: 100px;
}

.prep-dish {
	width: 200px;
	height: 200px;
	background-color: rgba(255,255,255,0.8);
	position: absolute;
	right: 10px;
	top: 74px;
}

.cook-dish {
	width: 200px;
	height: 200px;
	background-color: rgba(255,255,255,0.8);
	position: absolute;
	right: 10px;
	bottom: 264px;
}

.prep {
	text-align: center;
	font-size: 18px;
	font-weight: 700;
	color: #a47d5e;
	text-transform: uppercase;
	letter-spacing: 2px;
	padding-top: 50px;
}

.num {
	text-align: center;
	font-size: 60px;
	font-weight: 700;
	color: #56534c;
	letter-spacing: 4px;
}

.time {
	text-align: center;
	font-size: 18px;
	font-weight: 700;
	color: #979285;
	padding-top: 10px;
}

.ready-dish {
	width: 200px;
	height: 200px;
	background-color:#ffffff;
	background-image: url(./img-vector.png);
	background-repeat: no-repeat;
	background-size: cover;
	background-position: 50% 50%;
	position: absolute;
	right: 10px;
	bottom: 62px;

}

.ready-in {
	text-align: center;
	font-size: 18px;
	color: #a47d5e;
	text-transform: uppercase;
	padding: 20px 0 10px 0;
	letter-spacing: 2px;
}

.ready-num {
	text-align: center;
	font-size: 92px;
	color: #dfc691;
	letter-spacing: 2px;
}

.ready-time {
	text-align: center;
	font-size: 18px;
	color: #979285;
	padding: 20px 0;
	letter-spacing: 2px;
}

.btn-recipe {
	width: 170px;
	height: 60px;
	text-align: center;
	font-size: 18px;
	font-weight: 700;
	text-transform: uppercase;
	color: #dfc691;
	background-color: #ffffff;
	border: 1px dashed #dfc691;
	cursor: pointer;
	position: absolute;
	left: 100px;
	bottom: 80px;
	
}

.btn-recipe:hover {
	color: #a47d5e;
	border: 1px dashed #a47d5e;
}